<template>
    <div class="complaintReportingEvaluate">
        <!-- 头部 -->
        <navigation-bar pageName="评价" @onLeftClick="goBack">
          <slot slot="nav-right" name="right" >
            <div slot="right" class="rightBtn" @click="onRightClick">
                提交
            </div>
          </slot>
        </navigation-bar>
        <section class="content">
             <!-- 是否完成 -->
            <section class="isComplate">
                <div class="isComplateHeader">请选择是否完成</div>
                <div class="isComContent">
                    <van-radio-group v-model="radio">
                        <van-cell title="是 , 已完成"  clickable @click="radio = '1'">
                            <van-radio slot="right-icon" name="1" checked-color="#FB5768"/>
                        </van-cell>
                        <van-cell title="否 , 未完成"  clickable @click="radio = '2'">
                            <van-radio slot="right-icon" name="2" checked-color="#FB5768" />
                        </van-cell>
                    </van-radio-group>
                </div>
            </section>

            <!-- 已完成工单评价 -->
            <section class="ComplateBox" v-if='radio == 1'>
                <div>
                    <div class="area-box">
                        <textarea placeholder="请输入评价内容..." v-model="message" maxlength="200" @input="filterContent"></textarea>
                        <div class="num">{{message.length}}/200</div>
                    </div>
                </div>
                <div class="service">
                    <div class="serviceLeft">服务评分</div>
                    <div class="serviceRight">
                        <div class="serviceImg" v-for="(item,index) in xingNum" :key="index">
                            <img :src="item" @click="changeStar(index)">
                        </div>
                    </div>
                </div>
            </section>

            <!-- 未完成工单评价 -->
            <section class="NoComplateBox" v-if='radio == 2'>
                <div class="area-box">
                    <textarea placeholder="请输入评价内容..." v-model="message" maxlength="200" @input="filterContent"></textarea>
                    <div class="num">{{message.length}}/200</div>
                </div>
            </section>
        </section>



    </div>
</template>

<script>
// import {evaluateWorkOrder} from '@/services/internalWorkPlanService'
import { evaluateWorkOrder } from '../../services/internalWorkPlanService'
import { store } from "../../utils/store";
import { Toast } from 'vant';
import NavigationBar from "../title/NavigationBar";

export default {
    name:'complaintReportingEvaluate',
    components: { NavigationBar },
    data(){
        return{
            title:"评价",
            radio:'1',
            message:'',
            evalLeaval: 5,
            xingImg:require('@/assets/imgs/workPlan/icon_evaluate_red.png'),
            xingNoImg: require('@/assets/imgs/workPlan/icon_evaluate_gray.png'),
            xingNum:[
                require('@/assets/imgs/workPlan/icon_evaluate_red.png'),
                require('@/assets/imgs/workPlan/icon_evaluate_red.png'),
                require('@/assets/imgs/workPlan/icon_evaluate_red.png'),
                require('@/assets/imgs/workPlan/icon_evaluate_red.png'),
                require('@/assets/imgs/workPlan/icon_evaluate_red.png'),
            ],
            userInfo: null,
            workDetail: null,
        }
    },
    mounted(){

    },
    created() {
        this.workDetail = this.$route.params;
        this.userInfo = store.get("userInfo");
    },
    methods:{
        /**
         * 页面返回事件
         */
        goBack: function() {
            this.$router.back(-1);
        },
        onRightClick(){
            //完成 and 驳回
            let isDone = '0';
            if(this.radio == '1'){
                isDone = '1'
            };
            let data = {
                isDone: isDone, //结束标识 0 驳回 1 完成
                evalContg: this.message, //评价的内容
                evalLevel: this.evalLeaval, //评价的星级
                complCode: this.workDetail.complCode, //工单代码
                areaCode: this.workDetail.areaCode,
                origFlag:'1',// 0:业主评价 1：回访评价
                ownerCode: this.workDetail.ownerCode,
                roomCode: this.workDetail.roomCode,
                roomName: this.workDetail.roomName,
                evaluatorName: this.userInfo.USER_NAME,
                createdBy: this.userInfo.USER_CODE,
                userCode: this.userInfo.USER_CODE,
                closeType: "0"
            };
            evaluateWorkOrder(data).then(
                res => {
                    if(res.resultCode == '0'){
                        Toast("评价成功");
                        store.set('insideWorkDetailComplCode', this.workDetail.complCode);
                        this.$router.go(-1);
                    }else{
                        Toast(res.msg)
                    }
                },
                err => {
                    this.$toast(err);
                }
            )
        },
        changeStar(value){
            this.xingNum = [];
            this.evalLeaval = value + 1;
            let arr = [];
            for(let i = 0;i<5;i++){
                if(i <= value){
                    arr.push(this.xingImg);
                }else{
                    arr.push(this.xingNoImg);
                }
            }
            this.xingNum = arr;
        },

        /**
         * 禁止输入表情
         */
        filterContent: function() {
            this.message = this.message.replace(/[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF][\u200D|\uFE0F]|[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF]|[0-9|*|#]\uFE0F\u20E3|[0-9|#]\u20E3|[\u203C-\u3299]\uFE0F\u200D|[\u203C-\u3299]\uFE0F|[\u2122-\u2B55]|\u303D|[\A9|\AE]\u3030|\uA9|\uAE|\u3030/ig, "");
        },
    }
}
</script>

